import { useState } from "react"
import axios from "axios";

import SearchBar from "./components/SearchBar"
import Results from "./components/Results"
import AudioPlayer from 'react-h5-audio-player';
import './App.scss';

const BASE_URL = "/api"
//const BASE_URL = "http://192.168.0.10:5000/api"

function App() {

  const [songs, setSongs] = useState([])
  const [url, setUrl] = useState("")
  const [header, setHeader] = useState("")

  const doSearch = async (query) => {
    const res = await axios.get(BASE_URL + "/search", {params: {
      q: query
    }})
    setSongs(res.data)
  }

  const playSong = (song) => {
    setUrl(BASE_URL + `/song/${song.videoId}/audio`)
    setHeader(`${song.artist} - ${song.name}`)
    document.title = `${song.artist} - ${song.name}`

    if ('mediaSession' in navigator) {
      navigator.mediaSession.metadata = new window.MediaMetadata({
        title: song.name,
        artist: song.artist,
        artwork: [
          { src: song.thumbnail.url,   sizes: `${song.thumbnail.width}x${song.thumbnail.height}`,   type: 'image/jpg' },
        ]
      });
    }
  }

  return (
    <div>
      <SearchBar doSearch = {doSearch}/>
      <Results songs = {songs} playSong = {playSong}/>
      <div id="audioPlayer">
        <AudioPlayer src={url} header={header}/>
      </div>
    </div>
  );
}

export default App;
